import top_bmg from '@/assets/imgs/need_top.jpg'
import scroll from '../../components/scroll-text'
export default {

  components: { scroll },

  data() {
    return {
      top_bmg: top_bmg,
      lists:['根据中国残疾人联合会要求,调研残疾人需求, 请在10月前完成']   //轮播中的文字
    }
  },

  computed: {},

  beforeMount () {},

  mounted () {
    
  },

  methods: {
    goBack() {
      this.$router.go(-1);
    },
    go_ywj() {
      this.$router.push('/dmd/ser-evaluation');
